<template>
  <div class="laptop-slider">
    <div class="slider">
      <div v-for="(item, index) in items" :key="index" :slide-index="index"
      :class="`item${index}`" class="slide laptop">
      </div>
      <button class="slide-button left" @click="actionSlide('right')"></button>
      <button class="slide-button right" @click="actionSlide('left')"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'slider',
  data() {
    return {
      items: ['one', 'two', 'three'],
      objParent: [],
      objChild: [],
      propParent: {
        width: 0,
        height: 0,
        childWidth: 0,
        breaker: 0,
        points: [],
        size: [0, 0, 0],
        opacity: [-1, -1, -1],
        viewIndex: [-1, -1, -1],
        zIndex: [-1, -1, -1],
        position: {
          loc: 'center',
          offset: 0,
        },
        limits: {
          start: 0,
          end: 0,
        },
        defaultSelected: 1,
        sizeDenomination: 'rem',
        remSize: 0,
      },
      propChild: {
        itemCount: 4,
        offset: 18,
        width: 38,
        height: 23,
        animation: {
          prop: 'all',
          delay: 600,
          type: 'ease',
        },
      },
      selected: {
        point: 0,
        target: '',
        direction: '',
        left: 0,
        key: -1,
      },
      isSnapping: false,
    };
  },
  methods: {
    actionSlide(direction = 'right', invert = false) {
      if (this.isSnapping) return;
      let dir = '';
      if (invert) {
        if (direction === 'right') {
          dir = 'left';
        } else {
          dir = 'right';
        }
      } else {
        dir = direction;
      }
      if (dir === 'left') {
        this.arrayRotate(this.propParent.viewIndex, true);
      } else {
        this.arrayRotate(this.propParent.viewIndex);
      }
      this.isSnapping = true;
      this.propParent.viewIndex.forEach((itemIndex, index) => {
        const x = index;
        const obj = document.getElementsByClassName(`item${itemIndex}`)[0];
        this.setStyle(obj, {
          'z-index': this.propParent.zIndex[x],
          height: `${this.propParent.size[x].v.toFixed(1)}rem`,
          transition: this.getAnimation(),
          left: `${this.propParent.points[x]}rem`,
          opacity: `${this.propParent.opacity[x]}`,
        });
        setTimeout(() => {
          this.setStyle(obj, 'transition', '');
          this.isSnapping = false;
        }, 600);
      });
    },
    arrayRotate(arr, reverse) {
      if (reverse) arr.unshift(arr.pop());
      else arr.push(arr.shift());
      return arr;
    },
    getDirection(valFrom, valTo, pos) {
      if (valFrom > valTo) {
        return pos;
      }
      return pos * -1;
    },
    getSize(valFrom, valTo, cSize) {
      if (valFrom.v < valTo.v) {
        if (cSize > valTo.v) {
          return valTo.v;
        }
        return cSize + valTo.p;
      }
      if (cSize < valTo.v) {
        return valTo.v;
      }
      return cSize - valTo.p;
    },
    getObject(cls, parent = document) {
      const ret = parent.querySelectorAll(`.${cls}`);
      return ret || [];
    },
    getClientX(e) {
      return (e.clientX || e.changedTouches[0].clientX) / 12;
    },
    getAnimation() {
      const an = this.propChild.animation;
      return `${an.prop} ${an.delay}ms ${an.type}`;
    },
    setStyle(obj, prop, value) {
      const o = obj;
      if (typeof prop === 'string' || prop instanceof String) {
        o.style[prop] = value;
      } else {
        Object.keys(prop).forEach((key) => {
          o.style[key] = prop[key];
        });
      }
    },
    dragMouseDown(event) {
      if (this.isSnapping) return;
      const e = event || window.event;
      e.preventDefault();
      this.selected.point = this.getClientX(e);
      this.selected.target = e.target;
      document.onmouseup = this.closeDragElement;
      document.ontouchend = this.closeDragElement;
      document.onmousemove = this.elementDrag;
      document.ontouchmove = this.elementDrag;
    },
    elementDrag(event) {
      const e = event || window.event;
      e.preventDefault();
      const clientX = this.getClientX(e);
      if (this.selected.point === clientX) {
        return;
      }
      const pos = (this.selected.point - clientX);
      if (pos > 0) {
        this.selected.direction = 'left';
      } else if (pos < 0) {
        this.selected.direction = 'right';
      }
      const computed = this.pxToInt(this.selected.target) - pos;
      if (computed <= this.propParent.limits.start
      || computed >= (this.propParent.limits.end
      - (this.selected.target.getBoundingClientRect().width / 12))) {
        return;
      }
      this.selected.point = clientX;
      this.selected.left = this.selected.target.offsetLeft - pos;
      this.objChild.forEach((obj, index) => {
        let nextVal = 0;
        // let ItemPos = 0;
        // let calLeft = 0;
        // let calHeight = 0;
        // const x = obj;
        if (this.selected.direction === 'left') {
          nextVal = (index - 1) !== -1 ? index - 1 : this.objChild.length - 1;
          // ItemPos = this.getDirection(
          //   this.propParent.points[index], this.propParent.points[nextVal], pos,
          // );
          // calLeft = this.pxToInt(x) - ItemPos;
        } else {
          nextVal = (index + 1) !== this.objChild.length ? index + 1 : 0;
          // ItemPos = this.getDirection(
          //   this.propParent.points[index], this.propParent.points[nextVal], pos,
          // );
          // calLeft = this.pxToInt(x) + ItemPos;
        }
        // calHeight = this.getSize(
        //   this.propParent.size[index], this.propParent.size[nextVal], this.pxToInt(x, 'height'),
        // );
        // this.setStyle(x, 'left', `${calLeft}rem`);
        // console.log(calHeight);
        // this.setStyle(x, 'height', `${calHeight}rem`);
        if (this.calcDistance(this.propParent.points[nextVal], clientX) < 19) {
          try {
            document.ontouchend();
          } catch (err) {
            this.actionSlide(this.selected.direction, true);
            document.onmouseup = null;
            document.onmousemove = null;
            document.ontouchend = null;
            document.ontouchmove = null;
          }
        }
      });
    },
    pxToInt(x, prop = 'left') {
      return Number(x.style[prop].replace('px', '').replace('rem', ''));
    },
    calcDistance(a, b) {
      return Math.abs(a - b);
    },
    closeDragElement() {
      // stop moving when mouse button is released:
      this.actionSlide(this.selected.direction, true);
      document.onmouseup = null;
      document.onmousemove = null;
      document.ontouchend = null;
      document.ontouchmove = null;
    },
    computeDistance(loc) {
      const ret = [];
      this.propParent.points.forEach((value) => {
        const midPoint = value + (this.propChild.width / 2);
        const v = midPoint - loc;
        if (v < 0) {
          ret.push(v * -1);
        } else {
          ret.push(v);
        }
      });
      return ret;
    },
    setSizeProp(value) {
      return {
        v: value,
        p: value / (this.propParent.breaker * 10),
      };
    },
    getContainerWidth() {
      const w = this.objParent.getBoundingClientRect().width / 12;
      return w;
    },
    resize() {
      // return null;
      this.propParent.width = this.getContainerWidth();
      if (this.propParent.position.loc === 'center') {
        this.propParent.childWidth = (this.propChild.width * 3) - (this.propChild.offset * 2);
        this.propParent.position.offset = ((this.propParent.width / 2))
        - (this.propParent.childWidth / 2);
      }
      this.plotPoints(true);
      this.propParent.points.forEach((point, index) => {
        const vIndex = this.propParent.viewIndex[index];
        this.setStyle(this.objChild[vIndex], 'left', `${point}rem`);
      });
    },
    plotPoints(ref = false) {
      if (ref) {
        const f = this.propParent.size.reduce(
          (prev, current) => ((prev.v > current.v) ? prev : current),
        );
        this.propParent.defaultSelected = this.propParent.size.indexOf(f);
      }
      const leftObject = this.propParent.defaultSelected - 1 === -1
        ? this.propParent.size.length - 1
        : this.propParent.defaultSelected - 1;
      const rightObject = this.propParent.defaultSelected + 1 === this.propParent.size.length
        ? 0
        : this.propParent.defaultSelected + 1;
      this.propParent.points = [];
      this.objChild.forEach((obj, index) => {
        const pts = this.propParent.breaker * index;
        let comp = pts + this.propParent.position.offset;
        if (rightObject === index) {
          comp -= this.propChild.offset;
          this.propParent.limits.end = comp + this.propChild.width;
        } else if (leftObject === index) {
          comp += this.propChild.offset;
          this.propParent.limits.start = comp;
        } else if (this.propParent.defaultSelected === index) {
          // placeholder
        } else {
          comp = (this.propParent.breaker * this.propParent.defaultSelected)
          + this.propParent.position.offset;
        }
        this.propParent.points.push(comp - this.propChild.offset);
      });
    },
  },
  mounted() {
    const parent = this.getObject('slider')[0];
    this.objParent = parent;
    this.propParent.width = this.getContainerWidth();
    this.propParent.height = this.objParent.scrollHeight;
    this.objChild = this.getObject('slide', this.objParent);
    this.propChild.itemCount = this.objChild.length;
    this.propParent.breaker = this.propChild.width;
    if (this.propParent.position.loc === 'center') {
      this.propParent.childWidth = (this.propChild.width * 3) - (this.propChild.offset * 2);
      this.propParent.position.offset = ((this.propParent.width / 2))
      - (this.propParent.childWidth / 2);
    }
    this.propParent.limits.start = this.propParent.position.offset;
    this.propParent.limits.end = this.objParent.scrollWidth + this.propParent.position.offset;
    this.plotPoints();
    setTimeout(() => {
      this.objChild.forEach((obj, index) => {
        this.setStyle(obj, 'width', `${this.propChild.width}rem`);
        const left = this.propParent.points[index];
        this.setStyle(obj, 'left', `${left}rem`);
        if (this.propParent.defaultSelected + 1 === index) {
          this.propParent.zIndex[2] = 2;
          this.propParent.viewIndex[2] = index;
          this.propParent.size[2] = this.setSizeProp(this.propChild.height * 0.6);
          this.setStyle(obj, 'height', `${this.propParent.size[2].v}rem`);
          this.setStyle(obj, 'z-index', 2);
          this.propParent.opacity[2] = 0.6;
        } else if (this.propParent.defaultSelected - 1 === index) {
          this.propParent.zIndex[0] = 2;
          this.propParent.viewIndex[0] = index;
          this.propParent.size[0] = this.setSizeProp(this.propChild.height * 0.6);
          this.setStyle(obj, 'height', `${this.propParent.size[0].v}rem`);
          this.setStyle(obj, 'z-index', 2);
          this.propParent.opacity[0] = 0.6;
        } else if (this.propParent.defaultSelected === index) {
          this.propParent.zIndex[1] = 3;
          this.propParent.viewIndex[1] = index;
          this.propParent.size[1] = this.setSizeProp(this.propChild.height);
          this.setStyle(obj, 'height', `${this.propParent.size[1].v}rem`);
          this.setStyle(obj, 'z-index', 3);
          this.propParent.opacity[1] = 1;
        } else {
          this.propParent.zIndex.push(1);
          this.propParent.viewIndex.push(index);
          this.propParent.size[index] = this.setSizeProp(this.propChild.height * 0.5);
          this.setStyle(obj, 'height', `${this.propParent.size[index].v}rem`);
          this.setStyle(obj, 'z-index', 1);
          this.propParent.opacity[index] = 0.3;
        }
      });
    }, 100);
    window.onresize = this.resize;
    this.objChild.forEach((obj) => {
      const x = obj;
      x.onmousedown = this.dragMouseDown;
      x.ontouchstart = this.dragMouseDown;
    });
  },
};
</script>
